  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.m_container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 50%;
  left: 20%;
  height: 23px;
  width: 23px;
  border: 1px solid #eee;
  border-radius: 20%;
  -webkit-transform: translateY(-30%);
          transform: translateY(-30%);
}

/* When the checkbox is checked, add a blue background */
.m_container input:checked ~ .checkmark {
  background-color: #40A137;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
  border-radius: 10px;
}

/* Show the checkmark when checked */
.m_container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.m_container .checkmark:after {
  left: 8px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

